<style type="text/less" global>:global(.gm-html-box) {
  /* width: 100%; */
  padding-right: 10px;
}
:global(article) {
  /* overflow-x: auto; */
}
:global(article h1) {
  font-size: 26px;
  margin-bottom: 10px;
