}
:global(article h2) {
  font-size: 24px;
  margin-bottom: 10px;
  font-weight: bold;
}
:global(article h3) {
  font-size: 22px;
  margin-bottom: 10px;
  font-weight: bold;
}
:global(article h4) {
  font-size: 20px;
  margin-bottom: 10px;
  font-weight: bold;
}
:global(article h5) {
  font-size: 18px;
  margin-bottom: 10px;
